import { useEffect } from '@wordpress/element';
import { PluginPostStatusInfo } from '@wordpress/edit-post';
import { ToggleControl } from '@wordpress/components';

import { __, sprintf } from '@wp-plugins/i18n';

import { OverrideSettings } from './OverrideSettings';
import { useDataState, useUpdateField } from './data';
import { ProLabel } from './ProLabel';

import './styles.scss';

const wrapperStyle: React.CSSProperties = {
	display: 'flex',
	justifyContent: 'space-between',
	alignItems: 'flex-start',
	width: '100%',
	marginTop: '1rem',
};

export const SendToTelegram: React.FC = () => {
	const { data, savedData, isSaving, isDirty } = useDataState();
	const updateField = useUpdateField();

	useEffect(() => {
		if (isDirty && !isSaving && !savedData) {
			updateField('send2tg')(data.send2tg);
		}
	}, [data.send2tg, isDirty, isSaving, savedData, updateField]);

	return (
		<>
			<PluginPostStatusInfo>
				<div style={wrapperStyle}>
					<ToggleControl
						// the basic switch
						label={
							<>
								{__('Send to Telegram')}
								<ProLabel />
							</>
						}
						checked={data.send2tg}
						onChange={updateField('send2tg')}
					/>
					<OverrideSettings />
				</div>
			</PluginPostStatusInfo>
			<PluginPostStatusInfo>
				<ToggleControl
					// @ts-ignore
					disabled={!data.send2tg}
					label={sprintf(
						'%s (%s)',
						__('Force send'),
						sprintf(/* translators: %s Rule name */ __('Ignore %s rules.'), __('Use when'))
					)}
					checked={data.force_send}
					onChange={updateField('force_send')}
				/>
			</PluginPostStatusInfo>
		</>
	);
};
