import { useCallback, useState } from '@wordpress/element';
import { ToggleControl, Button, Modal } from '@wordpress/components';

import { sprintf, __ } from '@wp-plugins/i18n';

import { Instances } from '@wp-plugins/wptelegram-shared--pro-gb-ip';

import { GearIcon } from './GearIcon';
import { useDataState, useUpdateField, useInstanceData, useUpdateInstanceField } from './data';

export const OverrideSettings: React.FC = () => {
	const { data } = useDataState();
	const updateField = useUpdateField();

	const [isOpen, setOpen] = useState(false);
	const openModal = useCallback(() => setOpen(true), []);
	const closeModal = useCallback(() => setOpen(false), []);

	const onRequestClose = useCallback(
		(event) => {
			// Prevent modal close on media upload
			if (event.target.id === 'wptgpro-upload-media') {
				return;
			}
			closeModal();
		},
		[closeModal]
	);

	const getInstance = useInstanceData();
	const updateInstance = useUpdateInstanceField();

	return (
		<>
			<Button
				aria-label={__('Override settings')}
				disabled={!data.send2tg}
				icon={<GearIcon />}
				isSmall
				onClick={openModal}
			/>
			{isOpen && (
				<Modal
					isDismissible={false}
					title={sprintf('%s (%s)', __('Post to Telegram'), __('Override settings'))}
					onRequestClose={onRequestClose as any}
					className='wptelegram_pro_modal'
				>
					<div className='wptelegram_pro_modal__body'>
						<ToggleControl
							label={__('Override settings')}
							checked={data.override_switch}
							onChange={updateField('override_switch')}
						/>
						<Instances
							isDisabled={!data.override_switch}
							getInstance={getInstance}
							updateInstance={updateInstance}
						/>
						<Button className='wptelegram_pro_modal__body--submit' isPrimary onClick={closeModal}>
							{__('Save Changes')}
						</Button>
					</div>
				</Modal>
			)}
		</>
	);
};
