import { useDispatch, useSelect } from '@wordpress/data';
import { useCallback } from '@wordpress/element';

import type { Instance } from '@wp-plugins/wptelegram-shared--pro-gb-ip';
import { __KEY__ } from './constants';
import type { DataShape, DataState } from './types';

type UpdateField = <K extends keyof DataShape>(field: K) => (value: DataShape[K]) => void;

const DEFAULT_INSTANCE_DATA: Instance = {
	active: true,
	channels: [],
	delay: '0',
	disable_notification: false,
	message_template: '',
	send_featured_image: true,
	files: {},
};

const savedSettings: DataShape = (window['wptelegram_pro'] as any)?.savedSettings || {};
// Add default fields to instances
const defaultInstances = Object.entries(savedSettings.instances || {}).reduce(
	(instances, [id, instance]) => ({ ...instances, [id]: { ...DEFAULT_INSTANCE_DATA, ...instance } }),
	{}
);

let DEFAULT_DATA: DataShape = {
	override_switch: false,
	send2tg: true,
	force_send: false,
	source: 'BLOCK_EDITOR',
	...savedSettings,
	instances: defaultInstances,
};

export const useUpdateField = (): UpdateField => {
	const { editPost } = useDispatch('core/editor');
	const { data } = useDataState();

	return useCallback<UpdateField>(
		(field) => {
			return (value) => {
				const newData = { ...data, [field]: value };

				editPost({ [__KEY__]: newData }, { undoIgnore: true });

				// Update default data to ensure re-renders don't reset the data
				DEFAULT_DATA = newData;
			};
		},
		[data, editPost]
	);
};

export const useDataState = (): DataState => {
	return useSelect((select) => {
		const {
			getEditedPostAttribute,
			// required flags
			isSavingPost,
			isPublishingPost,
			isEditedPostDirty,
		} = select('core/editor');

		const savedData = getEditedPostAttribute<DataShape>(__KEY__);

		return {
			data: savedData || DEFAULT_DATA,
			isDirty: isEditedPostDirty<boolean>(),
			isSaving: isSavingPost() || isPublishingPost(),
			savedData,
		};
	}, []);
};

export const useInstanceData = (): ((id: number) => Instance) => {
	const { data } = useDataState();

	return useCallback((id) => data.instances[id], [data.instances]);
};

type UpdateInstanceField = <K extends keyof Instance>(id: number, field: K) => (value: Instance[K]) => void;
export const useUpdateInstanceField = (): UpdateInstanceField => {
	const { data } = useDataState();
	const updateField = useUpdateField();

	return useCallback<UpdateInstanceField>(
		(id, field) => {
			return (value) => {
				updateField('instances')({
					...data.instances,
					[id]: {
						...data.instances?.[id],
						[field]: value,
					},
				});
			};
		},
		[data.instances, updateField]
	);
};
